import React from 'react';
import ItemShowcase from '../components/ItemShowcase';
import ItemFilter from '../components/ItemFilter';
import CharPotential from '../components/CharPotential';
import { CalcIcon, OverviewIcon, FilterIcon } from '../components/Icon';
import PageWithTabs from '../components/PageWithTabs';

export default function Potential() {
    return (
        <PageWithTabs
            title='  '
            tabs={[
                { label: '', icon: OverviewIcon, content: <ItemShowcase /> },
                { label: '', icon: FilterIcon, content: <ItemFilter /> },
                { label: '', icon: CalcIcon, content: <CharPotential /> },
            ]}
        />
    )
}
